@mixin clearfix {
  &:after {
    content: '';
    display: block;
    clear: both;
  }
}

@mixin textShadow($color: #000, $alpha: 0.5) {
  text-shadow: 1px 1px 2px rgba($color, $alpha);
}

@mixin LinkArrow {
  position: relative;

  &:after {
    content: "";
    display: block;
    width: 5px;
    height: 5px;
    position: absolute;
    top: 50%;
    right: 10px;
    margin-top: -2.5px;
    border-top: 1px solid #ccc;
    border-right: 1px solid #ccc;
    transform: rotate(45deg);
  }
}
